#searchForm {
  text-align: center;
}

#product-container {
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
}

.product-card {
  text-align: center;
  background-color: #fff;
  border: 1px solid #ccc;
  flex-direction: column;
  width: 25%;
  margin: 10px;
  padding: 20px;
  display: flex;
  box-shadow: 0 0 5px #0000001a;
}

.product-card .image-container {
  flex: 1;
  justify-content: center;
  align-items: center;
  display: flex;
}

.product-card img {
  max-width: 100%;
  height: auto;
}

.product-card h2 {
  margin-bottom: 10px;
  font-size: 1.2em;
}

.product-card p {
  margin-bottom: 5px;
}

.product-card button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  padding: 8px 16px;
  transition: background-color .3s;
}

.product-card button:hover {
  background-color: #0056b3;
}

@media screen and (width <= 768px) {
  .product-card {
    width: calc(50% - 20px);
  }
}

@media screen and (width <= 480px) {
  .product-card {
    width: 100%;
  }
}
